import { transformAttributesForm } from '../data/transforms';

export const simplifyTransformAttributesForm = (defaultSignUpFields: any) => {
  if (!defaultSignUpFields?.length) return [];

  const params = {
    data: {
      attributesForm: { items: defaultSignUpFields },
    },
  };

  const { fields } = transformAttributesForm(params);

  return fields;
};
